<template>
    <div id="app">
        <div class="top-wrap">
            <header-top></header-top>
            <header-nav></header-nav>
        </div>
        <header-mobile class="is-mobile"></header-mobile>
        <div class="hd-content hd-content-mobile">
            <router-view></router-view>
        </div>
		<hd-footer></hd-footer>
    </div>
</template>
<script>
import headerTop from "./components/public/header";
import headerMobile from "./components/public/mobile-header";
import headerNav from "./components/public/nav";
import hdFooter from "./components/public/footer";
export default {
    name: 'main',
    components: {
        headerTop,
        headerNav,
        hdFooter,
        headerMobile
    },
    data() {
        return {
        };
    },
    mounted() {
        window.addEventListener('scroll', function(){
            var t = document.documentElement.scrollTop || document.body.scrollTop;
            var header = document.querySelector('.hd-header');
            var nav = document.querySelector('.hd-nav');

            if(t >= header.offsetHeight){
                header.className = 'hd-header bg-white is-pc header-fixed header-shadow';
            }else{
                header.className = 'hd-header bg-white is-pc header-fixed';
            }
        });
    },
    watch: {
        showSlider(val) {
            console.log(val);
        }
    }
};
</script>
<style lang="less">
.header-fixed{
    position: fixed;
    width: 100%;
    z-index: 9999;
    top: 0;
    left: 0;
}
.nav-fixed{
    margin-top: 64px;
}
.header-shadow{
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}
@media screen and (max-width: 479px) {
    .top-wrap {
    }
    .is-pc {
        display: none;
    }
    .is-mobile {    	
        display: block;
    }
    .hd-content-mobile{
        margin-top: 70px;
    }
}
@media screen and (min-width: 479px) {
    .is-pc {
        display: block;
    }
    .is-mobile {
        display: none;
    }
}
.animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}
@-webkit-keyframes slideInLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-2000px);
        transform: translateX(-2000px);
    }
    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}

@keyframes slideInLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-2000px);
        -ms-transform: translateX(-2000px);
        transform: translateX(-2000px);
    }
    100% {
        -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
        transform: translateX(0);
    }
}

.slideInLeft {
    -webkit-animation-name: slideInLeft;
    animation-name: slideInLeft
}

@-webkit-keyframes slideOutLeft {
    0% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(-2000px);
        transform: translateX(-2000px)
    }
}

@keyframes slideOutLeft {
    0% {
        -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
        transform: translateX(0)
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(-2000px);
        -ms-transform: translateX(-2000px);
        transform: translateX(-2000px)
    }
}

.slideOutLeft {
    -webkit-animation-name: slideOutLeft;
    animation-name: slideOutLeft
}
/* 单行省略 */
.ellipsis{
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
/* 多行省略 */
.ellipsis-2l{
	overflow : hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp:2;
	-webkit-box-orient: vertical;
}
.clear{
	clear: both;
}
</style>



