<template>
    <div class="pic-container">
        <div class="btn pre-btn" @click="prePic()"><i class="el-icon-arrow-left"></i></div>
        <template v-for="(val, index) in cdata">
            <div :height="picheight" :width="picwidth" :class="'pic'+(currentIndex-index)" @click="changeIndex(index)" v-bind:style="{backgroundImage:'url('+val.bgsrc+')'}" class="info">
               <span>
                   <p>{{val.name}}</p>
                   <p>{{val.world}}</p>
               </span>

            </div>
        </template>
        <div class="btn next-btn" @click="nextPic()"><i class="el-icon-arrow-right"></i></div>
    </div>
</template>

<script>
    export default {
        components: {
        },
        props: {
            cdata: {
                type: Array,
                required: true
            },
            picheight:{
                type:Number,
                default:400
            },
            picwidth:{
                type:Number,
                default:270
            },
            middle:{
                type:Number,
                default:-1
            }
        },
        computed: {
            total(){
                return this.cdata.length;
            },
            currTitle(){
                let obj = this.cdata[this.currentIndex];

                if(obj && obj.title){
                    return obj.title;
                }else{
                    return '';
                }
            },
            /*classFilter(){
                console.log(index);
                return 'pic'+this.currentIndex;
            },*/
            heightFilter(index){
                return parseInt((1-Math.abs(index-this.currentIndex)/10)*this.picheight)+'px';
            },
            widthFilter(index){
                return parseInt((1-Math.abs(index-this.currentIndex)/10)*this.picwidth)+'px';
            }
        },
        data() {
            return{
                currentIndex:0,
            };
        },
        mounted() {
            this.currentIndex = parseInt(this.total/2);
            if(this.middle>=0){
                this.currentIndex=this.middle;
            }
        },
        methods:{
            changeIndex(index){
                this.currentIndex = index;
            },
            prePic(){
                if(this.currentIndex-1>=0){
                    this.currentIndex--;
                }else{
                    this.currentIndex = this.total-1;
                }
            },
            nextPic(){
                if(this.currentIndex+1<this.total){
                    this.currentIndex++;
                }else{
                    this.currentIndex = 0;
                }
            }
        },
    };
</script>

<style lang="less" scoped>
.pic-container{
    width:100%;
    height:450px;
    position: relative;
    overflow: hidden;
    perspective: 400px; 
    transform-style: preserve-3d;
    transform-origin:50% 50%;
    -webkit-transform-origin:50% 50%;
    .info{
        position: absolute;
        width: 270px;
        height: 400px;
        left:50%;
        top:50%;
        cursor: pointer;
        text-align: center;
        transform: translate3d(-50%, -50%, 0);
         -webkit-transition: all 1s ease;
            -moz-transition: all 1s ease;
             -ms-transition: all 1s ease;
              -o-transition: all 1s ease;
                 transition: all 1s ease;
        /* box-shadow:0 5px 50px #afafaf; */
        span{
            background: rgba(0,0,0,.3);
            display: block;
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 120px;
            color: #ffffff;
            p:nth-child(1){
                font-size: 18px;
                padding: 20px;
                margin-top: 20px;
            }
            p:nth-child(2){
                font-size: 14px;
                display: none;
            }
        }
    }
    .pic0{
        z-index: 5;
        span{
            animation: spanMove .5s;
            height: 160px;
            p:nth-child(1){
                font-size: 18px;
                padding: 15px;
                margin-top: 0;
            }
            p:nth-child(2){
                font-size: 14px;
                padding: 15px;
                line-height: 30px;
                display: block;
                word-break:break-all;
                word-wrap:break-word ;
            }
        }
    }
    @keyframes spanMove
    {
        from {height: 120px;}
        to {height: 160px;}
    }
    .pic-1{
        z-index: 4;
        transform: translate3d(-150%, -50%, -50px);
    }
    .pic-2{
        z-index: 3;
        transform: translate3d(-190%, -50%, -80px);
    }
    .pic-3{
        z-index: 2;
        transform: translate3d(-230%, -50%, -100px);
    }
    .pic-4,.pic-5,.pic-6,.pic-7,.pic-8,.pic-9{
        z-index: 1;
        transform: translate3d(-230%, -50%, -100px);
        filter:alpha(opacity=0); -moz-opacity:0; -khtml-opacity: 0; opacity: 0;
    }
    .pic1{
        z-index: 4;
        transform: translate3d(50%, -50%, -50px);
    }
    .pic2{
        z-index: 3;
        transform: translate3d(90%, -50%, -80px);
    }
    .pic3{
        z-index: 2;
        transform: translate3d(130%, -50%, -100px);
    }
    .pic4, .pic5, .pic6, .pic7, .pic8, .pic9{
        z-index: 1;
        transform: translate3d(130%, -50%, -100px);
        filter:alpha(opacity=0); -moz-opacity:0; -khtml-opacity: 0; opacity: 0;
    }
    .btn{
        width:100px;
        height:100px;
        filter:alpha(opacity=20); -moz-opacity:0.2; -khtml-opacity: 0.2; opacity: 0.2;
        border-radius:18px;;
        text-align:center;
        z-index:6;
        padding:0;
        line-height:50px;
        cursor: pointer;
        &:hover{
            filter:alpha(opacity=70); -moz-opacity:0.7; -khtml-opacity: 0.7; opacity: 0.7;
        }
        i{
            font-size:50px;
        }
    }
    .pre-btn{
        position:absolute;
        left:0;
        top:50%;
        transform: translate3d(0, -50%,0);
    }
    .next-btn{
        position:absolute;
        right:0;
        top:50%;
        transform: translate3d(0, -50%,0);
    }
    .pic-title{
        color: #979797;
        z-index:7;
        font-family: "微软雅黑";
        font-size:16px;
        font-weight: bold;
        position:absolute;
        left:50%;
        bottom:0;
        transform: translate3d(-50%, 0, 0);
    }
}
</style>


