<template>
<div>
    <div class="type-prompt">
        <el-dialog title="请问您是" v-model="typePromptVisible" size="tiny">
            <span @click= "showSupply">
                <img src="../../assets/img/type/supply.png" alt="自媒体">
                自媒体
            </span>
            <span @click= "showHire">
                <img src="../../assets/img/type/hire.png" alt="企业">
                企业
            </span>
        </el-dialog>
    </div>
    <supply-form :supplyFormVisible="supplyFormVisible" v-on:increment1="incrementSupply"></supply-form>
    <hire-form :hireFormVisible="hireFormVisible" v-on:increment2="incrementHire"></hire-form>
</div>
</template>
<script>
import supplyForm from "./supply-form";
import hireForm from "./hire-form";
export default {
    components: {
        hireForm,
        supplyForm
    },
    props: {
        value: {
            required: true,
            type: Boolean,
            default: false
        }
    },
    computed:{
        typePromptVisible: {
            get(){
                return this.value;
            },
            set(val){
                this.$emit('input',val);
            }
        }
    },
    data() {
        return {
            supplyFormVisible: false,
            hireFormVisible :false
        };
    },
    methods: {
        showSupply() {
            this.typePromptVisible = false;
            this.supplyFormVisible = true;
        },
        showHire() {
            this.typePromptVisible = false;
            this.hireFormVisible = true;
        },
        incrementSupply(val) {
            this.supplyFormVisible = false;
        },
        incrementHire(val) {
            this.hireFormVisible = false;
        }
    }
};
</script>
<style lang="less">
    .type-prompt{
        .el-dialog__header{
            text-align: center;
            .el-dialog__title{
                font-weight: 500;
                font-size: 28px;
                color: #1f2d3d;
            }
        }
        .el-dialog{
            min-width: 400px;
            width: 800px;
            height: 488px;
        }
        .el-dialog__body{
            
            span{
                display: inline-block;
                text-align: center;
                width: 49%;
                font-size: 24px;
                img{
                    display: block;
                    margin: 0 auto;
                    padding: 50px 105px;
                    cursor: pointer;
                    width: 100%;
                }
            }
            span:nth-child(2){
                    border-left: 1px solid #E0E6ED;
            }
        }
    }
</style>


