<template>
	<div class="article" @click="fnGoDetail">
	    <p class="article-title ellipsis">
            我是标题我是标题我是标题我是标题我是标题
        </p>
        <ul class="article-info ellipsis">
            <li>小魔仙团队</li>
            <li>巴拉巴拉团队</li>
            <li>2017-03-28</li>
        </ul>
        <div class="article-img"></div>
        <p class="article-desc ellipsis">我是描述我是描述我是描述我是描述我是描述我是描述</p>
	</div>
</template>
<script>
export default {
    name: 'articleItem',
    data() {
        return {
        };
    },
    methods: {
        fnGoDetail() {
            this.$router.push({path: '/detail'});
        }
    }
};
</script>
<style lang="less">
.article{
    border: 1px solid #D3DCE6;
    border-radius: 6px;
    height: 100%;
    background: #FFFFFF;
    margin-bottom: 20px;
    width: 780px;
    height: 450px;
    padding: 20px;
    cursor: pointer;
    &:hover{
        box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
    }
    .article-title{
        color: #1F2D3D;
        font-size: 18px;
        margin-bottom: 14px;
    }
    .article-info{
        font-size: 14px;
        color: #5E6D82;
        margin-bottom: 14px;
        li{
            padding-right: 5px;
            display: inline-block;
            border-right: 1px solid #e0e6ed;
            &:last-child{
                border: none;
            }
        }
    }
    .article-img{
        border: 1px solid red;
        border-radius: 4px;
        width: 740px;
        height: 320px;
        margin-bottom: 14px;
    }
    .article-desc{
        color: #475669;
        font-size: 16px;
    }    
}
</style>

<style lang="less">
@media screen and (max-width: 479px) {
    .article{
        width: 100%;
    }
    .article-title{
        width: 100%;
    }
    .article-info{
        width: 100%;
    }
    .article-img{
        width: 100%!important;
    }
}
</style>



