<template>
    <div id="container">
        <div class="selected">
            <selected :name="labelName" v-if="labelName" :type="searchType"></selected>
        </div>
        <div class="article-wrapper hd-block">
            <article-item></article-item>
        </div>
        <div class="right-wrap hd-block">
            <news></news>
            <div class="hot-labels">
                <hot-labels></hot-labels>
            </div>
        </div>
        <div class="page-nav">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="100" layout="prev, pager, next, jumper" :total="4000">
            </el-pagination>
        </div>
    </div>
</template>
<script>
import articleItem from "./../../components/public/aritcle-item";
import news from "./../../components/public/news";
import hotLabels from "./../../components/public/labels";
import selected from "./../../components/public/selected";
export default {
    components: {
        articleItem,
        news,
        hotLabels,
        selected
    },
    data() {
        return {
            labelName: '',
            searchType: 1, //1 标签搜索 2关键词搜索
            currentPage: 1
        };
    },
    methods: {
        fnInitSelected() {
            var label = this.$route.query.label;
            var type = this.$route.query.type;

            if(label) {
                this.labelName = label;
            }

            if(type) {
                this.searchType = type;
            }
        },
        handleCurrentChange(val) {
            console.log(val);
        },
        handleSizeChange(val) {
            console.log(val);
        }
    },
    mounted() {
        this.fnInitSelected();
    },
    watch: {
        $route() {
            this.fnInitSelected();
        }
    }
};
</script>
<style lang="less">
#container {
    max-width: 1200px;
    margin: 30px auto;
    font-size: 0;
    min-height: 76vh;
}
.selected{
    max-width: 1200px;
    margin: 30px 0 10px 10px;
    font-size: 14px;
}
.article-wrapper {
    vertical-align: top;
    font-size: 14px;
    margin-right: 20px;
    margin-left: 10px;
}

.right-wrap {
    font-size: 14px;
}

.hot-labels {
    margin-top: 20px;
}
.page-nav{
    margin-top: 30px;
}
</style>
<style lang="less">
@media screen and (max-width: 479px) {
    .article-wrapper {
        margin: 0 10px;
        display: block;
    }
    .right-wrap{
        margin: 0 10px;
    }
}
</style>



