<template>
    <div>
        <div class="header-mobile">
            <el-menu default-active="2" class="el-menu-vertical-demo" theme="dark" @select="fnSelectMenu"  mode="horizontal" menu-trigger="click">
                <el-submenu index="1">
                    <template slot="title"><i class="el-icon-menu"></i>新媒体中心</template>
                    <el-menu-item-group title="友情链接">
                        <el-menu-item index="1-1">广告投放</el-menu-item>
                        <el-menu-item index="1-2">掌上易数</el-menu-item>
                        <el-menu-item index="1-3">关于我们</el-menu-item>
                    </el-menu-item-group>
                    <el-menu-item-group title="导航">
                        <el-menu-item index="2-1"><router-link to="/" exact>首页</router-link></el-menu-item>
                        <el-menu-item index="2-2"><router-link to="/media">社交化媒体</router-link></el-menu-item>
