</template>
<script>
export default {
    name: 'mobileHeader',
    data() {
        return {};
    },
    methods: {
        fnSelectMenu() {
            console.log(123);
        }
    }
};
</script>
<style lang="less">
</style>
<style lang="less">
.header-mobile {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    z-index: 99999;
    .el-submenu{
        width: 100%;
        .el-menu{
            background: #48576a!important;
        }
    }
    .el-submenu__title{
        border-bottom: none!important;
    }
    .el-menu-item{
        padding-left: 50px!important;
        background: #48576a!important;
        color: #bfcbd9!important;
        a{
            color: #bfcbd9;
        }
    }
    .el-menu--horizontal .el-submenu>.el-menu{
        border: none;
        top: 60px;
    }
}
</style>



