<template>
    <div class="social-wrap">
       <div class="left">
            <img class="inner-img" src="http://cdn.wxhand.com/nm/join/explosive.png"/>
        </div>
        <div class="desc">
            <h1>爆款内容原创研究中心</h1>
            <p>以打造高校爆款内容为目标，提供具有爆发性的深度内容，探索输出人格化和别具风格深度内容的方法，解读借势创作爆款内容的规律，揭秘借力百校联动活动的策划全过程，致力于持续打造和输出引爆高校的“爆款”</p>
        </div>
    </div>
</template>

<script>
    export default {
        components: {},
        data() {
            return {};
        },
        methods: {
        }
    };
</script>

<style lang="less" scoped>
.social-wrap{
    max-width: 1200px;
    margin: auto;
    text-align: center;
    .left{
        display: inline-block;
    }
    .desc{
        vertical-align: top;
        text-align: center;
        margin: auto;
        display: inline-block;
        max-width: 500px;
        h1{
            text-align: left;
            padding-left: 15px;
            color: #1f2d3d;
            font-size: 22px;
            line-height: 25px;
            border-left: 10px #75fff4 solid;
            margin-bottom: 18px;
        }
        p{
            padding-left: 28px;
            text-align: justify;
            font-size: 16px;
            color: #8492a6;
            line-height: 24px;
        }
    }
    .inner-img{
        width: 100%;
        
    }
}
@media screen and (max-width: 650px) {
    .social-wrap{
        width: 100%;
    }
    .left{
        width: 60%;
    }
    .inner-img{
        margin-top: 50px;
    }
    .desc{
        padding: 20px 10px 50px;
    }
}
@media screen and (min-width: 650px) {
    .social-wrap{
        width: 80%;
    }
    .left{
        width: 291px;
        margin-right: 40px;
    }
    .inner-img{
        margin: 50px 0;
        margin-right: 40px;
    }
    .desc{
        padding: 120px 10px;
    }
}
</style>


