<template>
    <div class="selected-wrap">
        <div class="hd-block">
            <template v-if="type == 1">已选标签：</template>
            <template v-else>已选关键词：</template>
        </div>
        <ul class="hd-block selected-lists">
            <li>{{name}}<i @click="fnClear">×</i></li>
        </ul>
    </div>
</template>
<script>
export default {
    name: 'selected',
    props: ['name', 'type'],
    data() {
        return {};
    },
    methods: {
        fnClear() {
            this.name = '';
        }
    }
};
</script>
<style lang="less">
.selected-lists {
    li {
        display: inline-block;
        padding: 5px;
        border: 1px solid #52b5fe;
        color: #52b5fe;
        border-radius: 4px;
        background: rgba(226, 240, 252, 0.9);
        margin-left: 20px;
        padding-left: 10px;
        i {
            display: inline-block;
            padding: 0 10px;
            cursor: pointer;
            font-size: 18px;
        }
    }
}
</style>
<style lang="less">
@media screen and (max-width: 479px) {
}
</style>



