<template>
    <div class="news-wrap">
        <p class="news">热门标签</p>
        <ul class="label-lists">
            <li v-for="(item, index) in labels" key="labels" @click="fnLabelsClick(item)">{{item.name}}</li>
        </ul>
    </div>
</template>
<script>
export default {
    name: 'news',
    data() {
        return {
            labels: [{
                id: 1,
                name: '一个字'
            }, {
                id: 2,
                name: '字1'
            }, {
                id: 3,
                name: '一字2'
            }, {
                id: 4,
                name: '一个3'
            }, {
                id: 5,
                name: '一个字3'
            }, {
                id: 6,
                name: '一字43'
            },{
                id: 7,
                name: '一个字12'
            }, {
                id: 8,
                name: '字12'
            }, {
                id: 9,
                name: '一字1'
            }, {
                id: 10,
                name: '一个sf'
            }, {
                id: 11,
                name: '一个字fas'
            }, {
                id: 12,
                name: '一字fs'
            }]
        };
    },
    methods: {
        fnLabelsClick(item) {
            this.$router.push({path: '/search', query: {label: item.name, type: 1}});
        }
    }
};
</script>
<style lang="less">
.label-lists {
    li {
        display: inline-block;
        background: #E5E9F2;
        padding: 6px;
        margin-right: 10px;
        margin-bottom: 10px;
        border-radius: 6px;
        cursor: pointer;
    }
}
</style>



