<template>
    <div class="social-wrap">
        <div class="left">
            <img class="inner-img" src="http://cdn.wxhand.com/nm/join/industry.png"/>
        </div>
        <div class="desc">
            <h1>创意产业研究中心</h1>
            <p>以因高校新媒体传播而带来产业结构变化为研究对象，探索本地高校新媒体商业联盟对行业品牌的改造，探索高校新媒体与线下实体结合的商业模式，解读品牌性新媒体的IP打造，致力于用高校新媒体改变品牌和行业</p>
        </div>
    </div>
</template>

<script>
    export default {
        components: {},
        data() {
            return {};
        },
        methods: {
        }
    };
</script>

<style lang="less" scoped>
.social-wrap{
    max-width: 1200px;
    margin: auto;
    text-align: center;
    .left{
        display: inline-block;
    }
    .desc{
        vertical-align: top;
        margin: auto;
        display: inline-block;
        max-width: 500px;
        h1{
            text-align: left;
            padding-left: 15px;
            color: #1f2d3d;
            font-size: 22px;
            line-height: 25px;
            border-left: 10px #ffb265 solid;
            margin-bottom: 18px;
        }
        p{
            padding-left: 28px;
            text-align: justify;
            font-size: 16px;
            color: #8492a6;
            line-height: 24px;
        }
    }
    .inner-img{
        width: 100%;
    }
}
@media screen and (max-width: 650px) {
    .left{
        width: 80%;
    }
    .inner-img{
        margin-top: 50px;
    }
    .desc{
        padding: 20px 10px 50px;
    }
}
@media screen and (min-width: 650px) {
    .left{
        width: 374px;
        margin-right: 40px;
    }
    .inner-img{
        margin: 50px 0;
        margin-right: 40px;
    }
    .desc{
        padding: 100px 10px;
    }
}
</style>


