<template>
    <div class="social-wrap">
        <div class="desc">
            <h1>社交化媒体研究中心</h1>
            <p>以高校微信公众号、微博、头条号、知乎、社群等社交化媒体为研究对象，解读打造独具影响力的高校新媒体的方法，最大化挖掘单一社交化媒体的商业价值，揭秘高校社交化媒体借力资本腾飞的策略，探讨高校新媒体的未来发展之路</p>
        </div>
        <div class="right">
            <img class="inner-img" src="http://cdn.wxhand.com/nm/join/social.png"/>
        </div>
    </div>
</template>

<script>
    export default {
        components: {},
        data() {
            return {};
        },
        methods: {
        }
    };
</script>

<style lang="less" scoped>
.social-wrap{
    max-width: 1200px;
    margin: auto;
    text-align: center;
    .right{
        display: inline-block;
    }
    .desc{
        vertical-align: top;
        margin: auto;
        display: inline-block;
        max-width: 500px;
        h1{
            text-align: left;
            padding-left: 15px;
            color: #1f2d3d;
            font-size: 22px;
            line-height: 25px;
            border-left: 10px #67deff solid;
            margin-bottom: 18px;
        }
        p{
            padding-left: 28px;
            text-align: justify;
            font-size: 16px;
            color: #8492a6;
            line-height: 24px;
        }
    }
    .inner-img{
        width: 100%;
    }
}
@media screen and (max-width: 650px) {
    .right{
        width: 80%;
    }
    .inner-img{
        margin-bottom: 50px;
    }
    .desc{
        padding: 50px 10px 20px;
    }
}
@media screen and (min-width: 650px) {
    .right{
        width: 374px;
    }
    .inner-img{
        margin: 50px 0;
    }
    .desc{
        padding: 100px 10px;
    }
}
</style>


