<template>
    <div class="join-wrap">
        <el-row>
            <el-col class="wrap banner-wrap" :span="24" :style="'backgroundImage: url(' + (banner) + ');'">
                <el-button class="banner-button" @click="opendDialog">加入我们</el-button>
            </el-col>
            <el-col class="wrap white-wrap social-wrap" :span="24">
                <social></social>
            </el-col>
            <el-col class="wrap explosive-wrap" :span="24">
                <explosive></explosive>
            </el-col>
            <el-col class="wrap white-wrap interaction-wrap" :span="24">
                <interaction></interaction>
            </el-col>
            <el-col class="wrap industry-wrap" :span="24">
                <industry></industry>
            </el-col>
            <el-col class="wrap white-wrap carouse-wrap" :span="24">
                <h1>资深讲师</h1>
                <carousel :cdata="teachers"></carousel>
            </el-col>
            <el-col class="grey-wrap media-wrap carouse-wrap" :span="24">
                <h1>校媒人</h1>
                <carousel :cdata="medias"></carousel>
            </el-col>
            <el-col class="company-wrap" :span="24">
                <company-info @showDialog="opendDialog"></company-info>
            </el-col>
        </el-row>
        <select-type v-model="typePromptVisible"></select-type>
    </div>
</template>

<script>
    import social from '../components/joinus/social.vue';
    import interaction from '../components/joinus/interaction.vue';
    import industry from '../components/joinus/industry.vue';
    import explosive from '../components/joinus/explosive.vue';
    import carousel from '../components/joinus/carousel.vue';
    import companyInfo from '../components/joinus/company-info.vue';
    import selectType from '../components/joinus/select-type-prompt.vue';
    export default {
        components: {
            social,
            interaction,
            industry,
            explosive,
            carousel,
            companyInfo,
            selectType
        },
        data() {
            return {
                typePromptVisible: false,
                banner: 'http://cdn.wxhand.com/wei/images/home/footer/bg_zsdx.png',
                teachers: [
                    {bgsrc:"http://lorempixel.com/279/400",name:"文熙",world:"周文熙 掌上大学著名产品经理 黑锅背负者 现被技术部收养"},
                    {bgsrc:"http://lorempixel.com/279/400",name:"文熙",world:"周文熙 掌上大学著名产品经理 黑锅背负者 现被技术部收养"},
                    {bgsrc:"http://lorempixel.com/279/400",name:"文熙",world:"周文熙 掌上大学著名产品经理 黑锅背负者 现被技术部收养"}
                ],
                medias: [
                    {bgsrc:"http://lorempixel.com/279/400",name:"文熙",world:"周文熙  掌上大学著名产品经理  黑锅背负者  现被技术部收养"},
                    {bgsrc:"http://lorempixel.com/279/400",name:"文熙",world:"周文熙  掌上大学著名产品经理  黑锅背负者  现被技术部收养"},
                    {bgsrc:"http://lorempixel.com/279/400",name:"文熙",world:"周文熙  掌上大学著名产品经理  黑锅背负者  现被技术部收养"}
                ]
            };
        },
        methods: {
            opendDialog(){
                this.typePromptVisible = true;
            }
        }
    };
</script>

<style lang="less" scoped>
    .join-wrap{
        background-color: #efefef;
        .wrap{
            margin-bottom: 20px;
            background-color: #F9FAFC;
        }
        .white-wrap{
            background-color: #fff;
        }
        .grey-wrap{
            background-color: #F9FAFC;
        }
        .carouse-wrap h1{
            text-align: center;
            margin: 50px 0 40px;
        }
        .banner-wrap{
            position: relative;
            height: 470px;
            background-position: center center;
            background-size:auto 100%;
            background-repeat: no-repeat;
            box-shadow:0 5px 5px 4px #aaa;
            .banner-button{
                position: absolute;
                bottom: 70px;
                left: 50%;
                transform: translateX(-50%);
                width: 200px;
                height: 70px;
                font-size: 24px;
                color: #fff;
                background-color: transparent;
                border: 1px solid #fff;
            }
        }
    }
</style>


