// Vue.config.productionTip = false;
import 'element-ui/lib/theme-default/index.css';
import './assets/css/reset.css';
import Vue from "vue";
import VueRouter from 'vue-router';
import ElementUI from 'element-ui';
import App from './App';
import routerConfig from '../base/router-config.js';
import urlConfig from '../base/url-config.js';
import axios from 'axios';
import axiosPost from './utils/js/axios';

Vue.use(axiosPost, axios);
Vue.use(VueRouter);
Vue.use(urlConfig);
Vue.use(ElementUI);

var router = new VueRouter({
    "mode": "history",
    "routes": routerConfig.router
});

/* eslint-disable no-new */
new Vue({
    "router": router,
    "render": h => h(App)
}).$mount('#app');




