<template>
    <div class="supply-form">
        <el-dialog title="自媒体填写表" v-model="supplyFormVisible" @close="fideOut">
            <el-form :model="form">
                <el-form-item label="平台名">
                    <el-input v-model="form.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="粉丝数">
                    <el-input v-model="form.fnas" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="学校/地区">
                    <el-input v-model="form.post" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="手机号">
                    <el-input v-model="form.phoneNumber" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="微信号">
                    <el-input v-model="form.weChat" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="运营经验">
                    <el-input v-model="form.experience" auto-complete="off" type="textarea"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="fideOut">立即创建</el-button>
                <el-button @click="fideOut">取 消</el-button>
                
            </div>
        </el-dialog>
    </div>
</template>


<script>
export default {
    props: ['supplyFormVisible'],
    data() {
        return {
            form: {
                name: '',       //企业名
                fans: '',       //粉丝数
                post: '',       //职位
                phoneNumber: '',//手机号
                weChat: '',     //微信号
                experience: ''  //运营经验
            },
        };
    },
    methods:{
        fideOut(){
            this.$emit('increment1',false);
        }
    }
};
</script>
<style lang="less">
    .supply-form{
        .el-dialog__header{
            text-align: center;
            .el-dialog__title{
                font-weight: 500;
            }
        }
        .el-dialog{
            min-width: 400px;
            max-width: 559px;
        }
        .el-dialog__body{
            padding: 30px 64px;
            .el-form-item__label{width: 20%;text-align: left;}
            .el-form-item__content{
                display: inline-block;
                width: 75%;
                .el-input{
                    width: 100%;
                }
                .el-textarea{
                    width: 100%;
                }
            }
        }
        .dialog-footer{
            margin: 0 auto;
            text-align: center;
            margin-bottom: 50px;
        }
    }
</style>


