<template>
    <div class="operate">
    	<div class="operate-wrap">
            <div class="prev-wrap hd-block">
                <p>上一篇</p>
                <p>已经是第一篇了哦</p>
            </div>
            <div class="next-wrap hd-block">
                <p>下一篇</p>
                <p>这里是文章标题</p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'operate',
    data() {
        return {
        };
    }
};
</script>
<style lang="less">
.operate{
    background: #ffffff;
}
.operate-wrap {
    max-width: 1120px;
    margin: 0 auto;
    overflow: hidden;
}
.prev-wrap{
    float: left;
}
.next-wrap{
    float: right;
}
.prev-wrap, .next-wrap{
    p{
        color: #8492A6;
        font-size: 16px;
        text-align: center;
        padding: 6px 0;
        cursor: pointer;
        &:nth-child(2n+1) {
            margin-top: 10px;
        }
        &:nth-child(2n) {
            margin-bottom: 5px;
        }
    }
}
</style>

<style lang="less">
@media screen and (max-width: 479px) {
}
</style>



