<template>
    <div class="social-wrap">
        <div class="desc">
            <h1>创意互动营销研究中心</h1>
            <p>以创意互动营销为研究目的，探索打造引爆高校的热点事件，解读刷屏级互动式H5的实现。探讨各行业品牌校园营销的创意和互动设计，致力于为企业校园营销在传播和转化上创造价值</p>
        </div>
        <div class="right">
            <img class="inner-img" src="http://cdn.wxhand.com/nm/join/interaction.png"/>
        </div>
    </div>
</template>

<script>
    export default {
        components: {},
        data() {
            return {};
        },
        methods: {
        }
    };
</script>

<style lang="less" scoped>
.social-wrap{
    max-width: 1200px;
    margin: auto;
    text-align: center;
    .right{
        display: inline-block;
    }
    .desc{
        vertical-align: top;
        margin: auto;
        display: inline-block;
        max-width: 500px;
        h1{
            text-align: left;
            padding-left: 15px;
            color: #1f2d3d;
            font-size: 22px;
            line-height: 25px;
            border-left: 10px #65ffc6 solid;
            margin-bottom: 18px;
        }
        p{
            padding-left: 28px;
            text-align: justify;
            font-size: 16px;
            color: #8492a6;
            line-height: 24px;
        }
    }
    .inner-img{
        width: 100%;
    }
}
@media screen and (max-width: 650px) {
    .right{
        width: 80%;
    }
    .inner-img{
        margin-bottom: 50px;
    }
    .desc{
        padding: 50px 10px 20px;
    }
}
@media screen and (min-width: 650px) {
    .right{
        width: 385px;
        margin-left: 40px;
    }
    .inner-img{
        margin: 50px 0;
    }
    .desc{
        padding: 100px 10px;
    }
}
</style>
<style lang="less">
</style>


