<template>
	<div>
	    <div class="hd-nav bg-white is-pc nav-fixed">
	        <div class="nav-wrap">
	            <nav class="inner hd-block">
	                <router-link to="/" exact>首页</router-link>
	                <router-link to="/media">社交化媒体</router-link>
	                <router-link to="/content">爆款内容</router-link>
	                <router-link to="/marketing">互动营销</router-link>
	                <router-link to="/creative">创意产业</router-link>
	                <router-link to="/join" class="join-us">加入我们</router-link>
	            </nav>
	            <div class="search-wrap hd-block">
	                <el-input placeholder="请输入关键词搜索" v-model="keyword" @change="fnInputChange">
	                    <el-button slot="append" icon="search" class="search-btn" @click="fnSearch"></el-button>
	                </el-input>
	            </div>
	        </div>
	    </div>
	</div>
</template>
<script>
export default {
    name: 'nav',
    data() {
        return {
            keyword: ''
        };
    },
    methods: {
        fnSearch() {
            this.$router.push({path: '/search', query: {label: this.keyword, type: 2}});
        },
        fnInputChange(val) {
            this.keyword = val;
            console.log(val);
        }
    }
};
</script>
<style lang="less">
.hd-nav{
	box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}
.nav-wrap {
    max-width: 1200px;
    height: 42px;
    margin: 0 auto;
    font-size: 0;
    .inner {
        a {
            display: inline-block;
            height: 42px;
            line-height: 42px;
            font-size: 16px;
            margin-right: 20px;
            color: #1F2D3D;
            padding-left: 5px;
            padding-right: 5px;
        }
    }
    .search-wrap {
        width: 250px;
        float: right;
        padding-top: 2px;
    }
    .join-us{
    	color: #F7BA2A!important;
    }
    .router-link-active{
    	color: #20A0FF!important;
    	border-bottom: 2px solid #20A0FF;
    }
    .el-input-group__append{
    	background-color: #20a0ff!important;
    	border: none!important;
    }
    .search-btn{
    	color: #fff!important;
    }
}
</style>



